.Player-Section {
  height: 300px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
}

.headingColor {
  text-align: center;
}

.prv-nxtSec {
  margin-top: 20px;
  width: 45%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
}
.imgDiv {
  margin-left: 250px;
  width: 30%;
  height: 80%;
}
.detailsDiv {
  margin-left: 40px;
  display: flex;
  flex-direction: column;
}
.headingtext {
  font-size: 20px;
}
.PrevNxtButton {
  outline: none;
  border: 0;
  padding: 20px;
  background-color: aquamarine;
  border-radius: 30%;
  cursor: pointer;
  color: #fff;
}
.unClickPrevNxtButton {
  cursor: not-allowed;
  outline: none;
  border: 0;
  color: #fff;
  padding: 20px;
  background-color: #a5aab9;
  border-radius: 30%;
}
